@use '../../../designs/Palettes.scss';

@mixin desktop-landscape {
  //--|ðŸ Š display: block; ðŸ ˆ|--//
  div[class*='size'] {
    display: grid;
    grid-template-rows: 4rem 1fr 4rem; //--|ðŸ Š rows ðŸ ˆ|--//
    grid-template-columns: 6rem repeat(3, 1fr) 6rem; //--|ðŸ Š columns ðŸ ˆ|--//
    aside[class*='aside'] {
      grid-row: 2 / 3; //--|ðŸ ˆ row-start / row-end ðŸ ˆ|--//
      display: inherit; //--|ðŸ ˆ display: grid; ðŸ ˆ|--//
      grid-template: 2fr repeat(5, 1fr) / repeat(2, 1fr); //--|ðŸ Š rows / columns ðŸ ˆ|--//
      button[class*='dar'] {
        grid-column: 1 / 3; //--|ðŸ ˆ col-start / col-end ðŸ ˆ|--//
      }
    }
    & {
      aside[class*='dar'] {
        grid-column: 2 / 3; //--|ðŸ ˆ col-start / col-end ðŸ ˆ|--//
        background: Palettes.$indexLand-light;
        //--|ðŸ ‹ Dark ðŸ ‹|--//
        button[class*='top_'] {
          grid-area: 1 / 1 / 2 / 2; //--|ðŸ ˆ row-start / col-start / row-end / col-end ðŸ ˆ|--//
          //--|ðŸ Š background: rgba(red, 0.25); ðŸ ˆ|--//
        }
        button[class*='bot_'] {
          grid-area: 1 / 2 / 2 / 3; //--|ðŸ ˆ row-start / col-start / row-end / col-end ðŸ ˆ|--//
          //--|ðŸ Š background: rgba(blue, 0.25); ðŸ ˆ|--//
        }

        button[class*='lef_'] {
          grid-area: 2 / 1 / 3 / 3; //--|ðŸ ˆ row-start / col-start / row-end / col-end ðŸ ˆ|--//
          //--|ðŸ Š background: rgba(green, 0.25); ðŸ ˆ|--//
        }

        button[class*='rig_'] {
          grid-area: 3 / 1 / 4 / 3; //--|ðŸ ˆ row-start / col-start / row-end / col-end ðŸ ˆ|--//
          //--|ðŸ Š background: rgba(orange, 0.25); ðŸ ˆ|--//
        }
        button[class*='cen_'] {
          grid-area: 4 / 1 / 5 / 3; //--|ðŸ ˆ row-start / col-start / row-end / col-end ðŸ ˆ|--//
          //--|ðŸ Š background: rgba(purple, 0.25); ðŸ ˆ|--//
        }
        button[class*='tex_'] {
          grid-area: 5 / 1 / 6 / 3; //--|ðŸ ˆ row-start / col-start / row-end / col-end ðŸ ˆ|--//
          //--|ðŸ Š background: rgba(maroon, 0.25); ðŸ ˆ|--//
        }
        button[class*='ico_'] {
          grid-area: 6 / 1 / 7 / 3; //--|ðŸ ˆ row-start / col-start / row-end / col-end ðŸ ˆ|--//
          //--|ðŸ Š background: rgba(turquoise, 0.25); ðŸ ˆ|--//
        }
      }
      aside[class*='med'] {
        grid-column: 3 / 4; //--|ðŸ ˆ col-start / col-end ðŸ ˆ|--//

        background: Palettes.$indexLand-medium;
        //--|ðŸ ‹ Medium ðŸ ‹|--//
        button[class*='top_'] {
          grid-area: 1 / 1 / 2 / 2; //--|ðŸ ˆ row-start / col-start / row-end / col-end ðŸ ˆ|--//
          //--|ðŸ Š background: rgba(red, 0.25); ðŸ ˆ|--//
        }
        button[class*='bot_'] {
          grid-area: 1 / 2 / 2 / 3; //--|ðŸ ˆ row-start / col-start / row-end / col-end ðŸ ˆ|--//
          //--|ðŸ Š background: rgba(blue, 0.25); ðŸ ˆ|--//
        }

        button[class*='lef_'] {
          grid-area: 2 / 1 / 3 / 3; //--|ðŸ ˆ row-start / col-start / row-end / col-end ðŸ ˆ|--//
          //--|ðŸ Š background: rgba(green, 0.25); ðŸ ˆ|--//
        }

        button[class*='rig_'] {
          grid-area: 3 / 1 / 4 / 3; //--|ðŸ ˆ row-start / col-start / row-end / col-end ðŸ ˆ|--//
          //--|ðŸ Š background: rgba(orange, 0.25); ðŸ ˆ|--//
