          //--|ðŸ Š background: rgba(maroon, 0.25); ðŸ ˆ|--//
        }
        button[class*='ico_'] {
          grid-area: 6 / 1 / 7 / 3; //--|ðŸ ˆ row-start / col-start / row-end / col-end ðŸ ˆ|--//
          //--|ðŸ Š background: rgba(turquoise, 0.25); ðŸ ˆ|--//
        }
      }
      aside[class*='lig'] {
        grid-column: 4 / 5; //--|ðŸ ˆ col-start / col-end ðŸ ˆ|--//
        background: Palettes.$indexLand-dark;
      }
