    &[class*='h2'] {
      display: hidden;
    }
    &[class*='h3'] {
      display: hidden;
    }
    &[class*='h4'] {
      display: hidden;
    }
    &[class*='h5'] {
      display: hidden;
    }
    &[class*='h6'] {
      display: hidden;
    }
    &[class*='p'] {
      display: hidden;
    }
  }

  //--|ðŸ ‹ Desktop (Landscape) ðŸ ‹|--//
  @media screen and (max-height: 768px) {
  }
