//--|ðŸ Š Button_presets.scss ðŸ ˆ|--//
@mixin button-presets {
  //--|ðŸ Š button[class*='default'] ðŸ ˆ|--//
  //--|ðŸ ‰ display: flex; ðŸ ‰|--//
  display: flex;
  position: relative;
  div {
    width: 100%;
    height: 100%;
    display: grid;
    position: absolute;
    *[class*='_text'],
    *[class*='_back'] {
      margin: 0;
      display: flex;
      overflow: hidden;
      align-items: center;
      span {
        flex-grow: 1;
        overflow: hidden;
        text-overflow: ellipsis;
      }
    }
    img[class*='_icon'] {
      overflow: hidden;
      mask-size: contain;
      mask-position: center;
      mask-repeat: no-repeat;
    }

    &[class*='_text'] {
      z-index: 2;
    }
    &[class*='_icon'] {
      z-index: 1;
    }
    &[class*='_back'] {
      z-index: 0;
    }
  }

  @extend %toggle-radius; //--|ðŸ ˆ Resize Radius ðŸ ˆ|--//
  @extend %toggle-cursor; //--|ðŸ ˆ Apply Cursor ðŸ ˆ|--//
  @extend %toggle-symbol; //--|ðŸ ˆ Remove Text ðŸ ˆ|--//
}

%toggle-cursor {
  //--|ðŸ ‹ Toggle Cursor ðŸ ‹|--//
  div[class*='_text'],
  div[class*='_icon'] {
    span,
    img {
      cursor: pointer;
    }
  }
}
%toggle-symbol {
  //--|ðŸ ‹ Hide Glitch ðŸ ‹|--//
  .graphic {
    //--|ðŸ Š div[class*='graphic'] ðŸ ˆ|--//
    img[class*='_icon'] {
      color: rgba(255, 255, 255, 0);
    }
  }
  .missing {
    //--|ðŸ Š div[class*='missing'] ðŸ ˆ|--//
    img[class*='_icon'] {
      display: none;
    }
  }
}
%toggle-radius {
  //--|ðŸ ‹ Border Radius ðŸ ‹|--//
  div {
    *[class*='_text'],
    *[class*='_back'] {
      border-radius: 10px;
      //--|ðŸ Š background: purple !important; ðŸ ˆ|--//
    }
    //--|ðŸ ‹ Desktop (Landscape) ðŸ ‹|--//
    @media screen and (max-height: 768px) {
      *[class*='_text'],
      *[class*='_back'] {
        border-radius: 8px;
        //--|ðŸ Š background: red !important; ðŸ ˆ|--//
      }
    }
    @media screen and (max-height: 480px) {
      *[class*='_text'],
      *[class*='_back'] {
        border-radius: 6px;
        //--|ðŸ Š background: green !important; ðŸ ˆ|--//
      }
    }
    @media screen and (max-height: 360px) {
      *[class*='_text'],
      *[class*='_back'] {
        border-radius: 4px;
        //--|ðŸ Š background: blue !important; ðŸ ˆ|--//
      }
    }
  }
}
