//--|ðŸ Š Button_placing.scss ðŸ ˆ|--//
@mixin desktop-placing {
  //--|ðŸ ‰ display: flex; ðŸ ‰|--//
  //--|ðŸ ‹ Desktop (Landscape) ðŸ ‹|--//
  //--|ðŸ Š button[class*='default'] ðŸ ˆ|--//
  div[class*='_top_'] {
    //--|ðŸ Š display: grid; ðŸ ˆ|--//
    grid-template-columns: 8px 1fr 8px; //--|ðŸ Š columns ðŸ ˆ|--//
    grid-template-rows: 8px 4rem 1fr 8px; //--|ðŸ Š rows ðŸ ˆ|--//
    *[class*='_text'],
    *[class*='_back'] {
      //--|ðŸ Š display: flex; ðŸ ˆ|--//
      grid-area: 2 / 2 / 3 / 3; //--|ðŸ ˆ row-start / col-start / row-end / col-end ðŸ ˆ|--//
    }
    img[class*='_icon'] {
      grid-area: 3 / 2 / 4 / 3; //--|ðŸ ˆ row-start / col-start / row-end / col-end ðŸ ˆ|--//
    }
  }
  div[class*='_bot_'] {
    grid-template-columns: 8px 1fr 8px; //--|ðŸ Š columns ðŸ ˆ|--//
    grid-template-rows: 8px 1fr 4rem 8px; //--|ðŸ Š rows ðŸ ˆ|--//
    *[class*='_text'],
    *[class*='_back'] {
      //--|ðŸ Š display: flex; ðŸ ˆ|--//
      grid-area: 3 / 2 / 4 / 3; //--|ðŸ ˆ row-start / col-start / row-end / col-end ðŸ ˆ|--//
    }
    img[class*='_icon'] {
      grid-area: 2 / 2 / 3 / 3; //--|ðŸ ˆ row-start / col-start / row-end / col-end ðŸ ˆ|--//
    }
  }
  div[class*='_lef_'] {
    //--|ðŸ Š display: grid; ðŸ ˆ|--//
    &.graphic {
      grid-template-rows: 8px 1fr 4rem 1fr 8px; //--|ðŸ Š rows ðŸ ˆ|--//
      grid-template-columns: 8px 4rem auto 1fr 8px; //--|ðŸ Š columns ðŸ ˆ|--//
      *[class*='_text'],
      *[class*='_back'] {
        //--|ðŸ Š display: flex; ðŸ ˆ|--//
        grid-area: 3 / 2 / 4 / 4; //--|ðŸ ˆ row-start / col-start / row-end / col-end ðŸ ˆ|--//
      }
      img[class*='_icon'] {
        grid-area: 3 / 2 / 4 / 3; //--|ðŸ ˆ row-start / col-start / row-end / col-end ðŸ ˆ|--//
      }
    }
    &.missing {
      grid-template-rows: 8px 1fr 4rem 1fr 8px; //--|ðŸ Š rows ðŸ ˆ|--//
      grid-template-columns: 8px auto 1fr 8px; //--|ðŸ Š columns ðŸ ˆ|--//
      *[class*='_text'],
      *[class*='_back'] {
        //--|ðŸ Š display: flex; ðŸ ˆ|--//
        grid-area: 3 / 2 / 4 / 3; //--|ðŸ ˆ row-start / col-start / row-end / col-end ðŸ ˆ|--//
      }
      img[class*='_icon'] {
        display: none;
      }
    }
  }
  div[class*='_rig_'] {
    //--|ðŸ Š display: grid; ðŸ ˆ|--//
    &.graphic {
      grid-template-rows: 8px 1fr 4rem 1fr 8px; //--|ðŸ Š rows ðŸ ˆ|--//
      grid-template-columns: 8px 1fr auto 4rem 8px; //--|ðŸ Š columns ðŸ ˆ|--//
      *[class*='_text'],
      *[class*='_back'] {
        //--|ðŸ Š display: flex; ðŸ ˆ|--//
        grid-area: 3 / 3 / 4 / 5; //--|ðŸ ˆ row-start / col-start / row-end / col-end ðŸ ˆ|--//
      }
      img[class*='_icon'] {
        grid-area: 3 / 4 / 4 / 5; //--|ðŸ ˆ row-start / col-start / row-end / col-end ðŸ ˆ|--//
      }
    }
    &.missing {
      grid-template-rows: 8px 1fr 4rem 1fr 8px; //--|ðŸ Š rows ðŸ ˆ|--//
      grid-template-columns: 8px 1fr auto 8px; //--|ðŸ Š columns ðŸ ˆ|--//
      *[class*='_text'],
      *[class*='_back'] {
        //--|ðŸ Š display: flex; ðŸ ˆ|--//
        grid-area: 3 / 3 / 4 / 4; //--|ðŸ ˆ row-start / col-start / row-end / col-end ðŸ ˆ|--//
      }
      img[class*='_icon'] {
        display: none;
      }
    }
  }
  div[class*='_cen_'] {
    //--|ðŸ Š display: grid; ðŸ ˆ|--//
    grid-template-rows: 8px 1fr 4rem 1fr 8px; //--|ðŸ Š rows ðŸ ˆ|--//
    grid-template-columns: 8px 1fr 4rem 1fr 8px; //--|ðŸ Š columns ðŸ ˆ|--//
    *[class*='_text'],
    *[class*='_back'] {
      //--|ðŸ Š display: flex; ðŸ ˆ|--//
      grid-area: 3 / 2 / 4 / 5; //--|ðŸ ˆ row-start / col-start / row-end / col-end ðŸ ˆ|--//
    }
    img[class*='_icon'] {
      grid-area: 3 / 3 / 4 / 4; //--|ðŸ ˆ row-start / col-start / row-end / col-end ðŸ ˆ|--//
    }
  }
  div[class*='_tex_'] {
    //--|ðŸ Š display: grid; ðŸ ˆ|--//
    grid-template-rows: 8px 1fr 4rem 1fr 8px; //--|ðŸ Š rows ðŸ ˆ|--//
    grid-template-columns: 8px 1fr auto 1fr 8px; //--|ðŸ Š columns ðŸ ˆ|--//
    *[class*='_text'],
    *[class*='_back'] {
      //--|ðŸ Š display: flex; ðŸ ˆ|--//
      grid-area: 3 / 3 / 4 / 4; //--|ðŸ ˆ row-start / col-start / row-end / col-end ðŸ ˆ|--//
    }
    img[class*='_icon'] {
      display: none;
    }
  }
  div[class*='_ico_'] {
    //--|ðŸ Š display: grid; ðŸ ˆ|--//
    grid-template-rows: 2px 1fr 2px; //--|ðŸ Š rows ðŸ ˆ|--//
    grid-template-columns: 2px 1fr auto 1fr 2px; //--|ðŸ Š columns ðŸ ˆ|--//

    &[class*='_text'] {
      display: none;
    }
    *[class*='_back'] {
      grid-area: 2 / 3 / 3 / 4; //--|ðŸ ˆ row-start / col-start / row-end / col-end ðŸ ˆ|--//
      span {
        display: none;
      }
    }
    img[class*='_icon'] {
      grid-area: 2 / 3 / 3 / 4; //--|ðŸ ˆ row-start / col-start / row-end / col-end ðŸ ˆ|--//
    }
  }

  //--|ðŸ ‹ Desktop (Landscape) ðŸ ‹|--//
  @media screen and (max-height: 768px) {
    div[class*='_top_'] {
      //--|ðŸ Š display: grid; ðŸ ˆ|--//
      grid-template-columns: 6px 1fr 6px; //--|ðŸ Š columns ðŸ ˆ|--//
      grid-template-rows: 6px 3rem 1fr 6px; //--|ðŸ Š rows ðŸ ˆ|--//
    }
    div[class*='_bot_'] {
      //--|ðŸ Š display: grid; ðŸ ˆ|--//
      grid-template-columns: 6px 1fr 6px; //--|ðŸ Š columns ðŸ ˆ|--//
      grid-template-rows: 6px 1fr 3rem 6px; //--|ðŸ Š rows ðŸ ˆ|--//
    }
    div[class*='_lef_'] {
      //--|ðŸ Š display: grid; ðŸ ˆ|--//
      &.graphic {
        grid-template-rows: 6px 1fr 3rem 1fr 6px; //--|ðŸ Š rows ðŸ ˆ|--//
        grid-template-columns: 6px 3rem auto 1fr 6px; //--|ðŸ Š columns ðŸ ˆ|--//
      }
      &.missing {
        grid-template-rows: 8px 1fr 3rem 1fr 8px; //--|ðŸ Š rows ðŸ ˆ|--//
        grid-template-columns: 8px auto 1fr 8px; //--|ðŸ Š columns ðŸ ˆ|--//
      }
    }
    div[class*='_rig_'] {
      //--|ðŸ Š display: grid; ðŸ ˆ|--//
      &.graphic {
        grid-template-rows: 6px 1fr 3rem 1fr 6px; //--|ðŸ Š rows ðŸ ˆ|--//
        grid-template-columns: 6px 1fr auto 3rem 6px; //--|ðŸ Š columns ðŸ ˆ|--//
      }
      &.missing {
        grid-template-rows: 6px 1fr 3rem 1fr 6px; //--|ðŸ Š rows ðŸ ˆ|--//
        grid-template-columns: 6px 1fr auto 6px; //--|ðŸ Š columns ðŸ ˆ|--//
      }
    }
    div[class*='_cen_'] {
      //--|ðŸ Š display: grid; ðŸ ˆ|--//
      grid-template-rows: 6px 1fr 3rem 1fr 6px; //--|ðŸ Š rows ðŸ ˆ|--//
      grid-template-columns: 6px 1fr 3rem 1fr 6px; //--|ðŸ Š columns ðŸ ˆ|--//
    }
    div[class*='_tex_'] {
      //--|ðŸ Š display: grid; ðŸ ˆ|--//
      grid-template-rows: 6px 1fr 3rem 1fr 6px; //--|ðŸ Š rows ðŸ ˆ|--//
      grid-template-columns: 6px 1fr auto 1fr 6px; //--|ðŸ Š columns ðŸ ˆ|--//
    }
  }
  @media screen and (max-height: 480px) {
    div[class*='_top_'] {
      //--|ðŸ Š display: grid; ðŸ ˆ|--//
      grid-template-columns: 4px 1fr 4px; //--|ðŸ Š columns ðŸ ˆ|--//
      grid-template-rows: 4px 2rem 1fr 4px; //--|ðŸ Š rows ðŸ ˆ|--//
    }
    div[class*='_bot_'] {
      //--|ðŸ Š display: grid; ðŸ ˆ|--//
      grid-template-columns: 4px 1fr 4px; //--|ðŸ Š columns ðŸ ˆ|--//
      grid-template-rows: 4px 1fr 2rem 4px; //--|ðŸ Š rows ðŸ ˆ|--//
    }
    div[class*='_lef_'] {
      //--|ðŸ Š display: grid; ðŸ ˆ|--//
      &.graphic {
        grid-template-rows: 4px 1fr 2rem 1fr 4px; //--|ðŸ Š rows ðŸ ˆ|--//
        grid-template-columns: 4px 2rem auto 1fr 4px; //--|ðŸ Š columns ðŸ ˆ|--//
      }
      &.missing {
        grid-template-rows: 4px 1fr 2rem 1fr 4px; //--|ðŸ Š rows ðŸ ˆ|--//
        grid-template-columns: 4px auto 1fr 4px; //--|ðŸ Š columns ðŸ ˆ|--//
      }
    }
    div[class*='_rig_'] {
      //--|ðŸ Š display: grid; ðŸ ˆ|--//

      &.graphic {
        grid-template-rows: 4px 1fr 2rem 1fr 4px; //--|ðŸ Š rows ðŸ ˆ|--//
        grid-template-columns: 4px 1fr auto 2rem 4px; //--|ðŸ Š columns ðŸ ˆ|--//
      }
      &.missing {
        grid-template-rows: 4px 1fr 2rem 1fr 4px; //--|ðŸ Š rows ðŸ ˆ|--//
        grid-template-columns: 4px 1fr auto 4px; //--|ðŸ Š columns ðŸ ˆ|--//
      }
    }
    div[class*='_cen_'] {
      //--|ðŸ Š display: grid; ðŸ ˆ|--//
      grid-template-rows: 4px 1fr 2rem 1fr 4px; //--|ðŸ Š rows ðŸ ˆ|--//
      grid-template-columns: 4px 1fr 2rem 1fr 4px; //--|ðŸ Š columns ðŸ ˆ|--//
    }
    div[class*='_tex_'] {
      //--|ðŸ Š display: grid; ðŸ ˆ|--//
      grid-template-rows: 4px 1fr 2rem 1fr 4px; //--|ðŸ Š rows ðŸ ˆ|--//
      grid-template-columns: 4px 1fr auto 1fr 4px; //--|ðŸ Š columns ðŸ ˆ|--//
    }
  }
  @media screen and (max-height: 360px) {
    div[class*='_top_'] {
      //--|ðŸ Š display: grid; ðŸ ˆ|--//
      grid-template-columns: 2px 1fr 2px; //--|ðŸ Š columns ðŸ ˆ|--//
      grid-template-rows: 2px 1.5rem 1fr 2px; //--|ðŸ Š rows ðŸ ˆ|--//
    }
    div[class*='_bot_'] {
      //--|ðŸ Š display: grid; ðŸ ˆ|--//
      grid-template-columns: 2px 1fr 2px; //--|ðŸ Š columns ðŸ ˆ|--//
      grid-template-rows: 2px 1fr 1.5rem 2px; //--|ðŸ Š rows ðŸ ˆ|--//
    }
    div[class*='_lef_'] {
      //--|ðŸ Š display: grid; ðŸ ˆ|--//
      &.graphic {
        grid-template-rows: 2px 1fr 1.5rem 1fr 2px; //--|ðŸ Š rows ðŸ ˆ|--//
        grid-template-columns: 2px 1.5rem auto 1fr 2px; //--|ðŸ Š columns ðŸ ˆ|--//
      }
      &.missing {
        grid-template-rows: 2px 1fr 1.5rem 1fr 2px; //--|ðŸ Š rows ðŸ ˆ|--//
        grid-template-columns: 2px auto 1fr 2px; //--|ðŸ Š columns ðŸ ˆ|--//
      }
    }
    div[class*='_rig_'] {
      //--|ðŸ Š display: grid; ðŸ ˆ|--//
      &.graphic {
        grid-template-rows: 2px 1fr 1.5rem 1fr 2px; //--|ðŸ Š rows ðŸ ˆ|--//
        grid-template-columns: 2px 1fr auto 1.5rem 2px; //--|ðŸ Š columns ðŸ ˆ|--//
      }
      &.missing {
        grid-template-rows: 2px 1fr 1.5rem 1fr 2px; //--|ðŸ Š rows ðŸ ˆ|--//
        grid-template-columns: 2px 1fr auto 2px; //--|ðŸ Š columns ðŸ ˆ|--//
      }
    }
    div[class*='_cen_'] {
      //--|ðŸ Š display: grid; ðŸ ˆ|--//
      grid-template-rows: 2px 1fr 1.5rem 1fr 2px; //--|ðŸ Š rows ðŸ ˆ|--//
      grid-template-columns: 2px 1fr 1.5rem 1fr 2px; //--|ðŸ Š columns ðŸ ˆ|--//
    }
    div[class*='_tex_'] {
      //--|ðŸ Š display: grid; ðŸ ˆ|--//
      grid-template-rows: 2px 1fr 1.5rem 1fr 2px; //--|ðŸ Š rows ðŸ ˆ|--//
      grid-template-columns: 2px 1fr auto 1fr 2px; //--|ðŸ Š columns ðŸ ˆ|--//
    }
  }
}
