@mixin desktop-spacing {
  //--|ðŸ ‰ display: flex; ðŸ ‰|--//
  //--|ðŸ ‹ Desktop (Landscape) ðŸ ‹|--//
  //--|ðŸ Š button[class*='default'] ðŸ ˆ|--//
  div[class*='_top_'],
  div[class*='_bot_'] {
    //--|ðŸ Š display: grid; ðŸ ˆ|--//
    *[class*='_text'],
    *[class*='_back'] {
      //--|ðŸ Š display: flex; ðŸ ˆ|--//
      span {
        padding: 0 8px;
        text-align: center;
      }
    }
  }
