    //--|ðŸ Š display: grid; ðŸ ˆ|--//
    &.graphic {
      *[class*='_text'],
      *[class*='_back'] {
        //--|ðŸ Š display: flex; ðŸ ˆ|--//

        text-align: left;
        padding: 0 8px 0 64px; //--|ðŸ ˆ top / right / bottom / left ðŸ ˆ|--//
      }
    }
    &.missing {
      *[class*='_text'],
      *[class*='_back'] {
        //--|ðŸ Š display: flex; ðŸ ˆ|--//
        padding: 0 8px; //--|ðŸ ˆ top & bottom / left & right ðŸ ˆ|--//
        text-align: left;
